@font-face {
    font-family: 'fontGame';
    src: url('./font/Pony.ttf')
}

.gameOanTuXi{
    font-family: fontGame;
    position: fixed;
    width: 100%;
    height: 100%;
    background-image: url('./assets/bgGame.png');
    background-size: 100%;
    background-repeat: no-repeat;
}

.speech-bubble {
    position: relative;
    background: #45D994;
    border-radius: .4em;
}

.speech-bubble:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 0;
    border: 35px solid transparent;
    border-top-color: #45D994;
    border-bottom: 0;
    border-left: 0;
    margin-left: -17.5px;
    margin-bottom: -35px;
}

.playerGame{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.theThink{
    border: 5px solid #45D994;
    width: 130px;
    height: 130px;
    background: #fff;
    border-radius: 10px;
}

.btnItem{
    width:60px;
    height:60px;
}

.btnItem>img{
    width:45px;
    height: 45px;
}